.float-chart-container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  min-height: 300px;
  max-height: 100%;
  &.float-chart-container-mini {
    min-height: 285px;
  }
}

.float-chart {
  width: 100%;
  position: relative;
  canvas{
    position:absolute !important;
    top: 0;
    bottom: 0;
  }
  max-width: none;
  height: 400px;
  &.float-chart-mini {
    height: 270px;
  }
}
